body {
  margin: 0;
  font-family: 'Poppins', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

a:-webkit-any-link {
	text-decoration: none !important;	
}
.bg {
    background: url(/static/media/footer.8647063a.png) no-repeat 0% 0%;
    background-size: 100%;
    text-align: center;
    vertical-align: center;
    width: 100%;
    height: 100%;
    margin-bottom: 0;
}


@media (max-width: 900px) {
    .bg{
      height: 200%;
      resize: both;
      background-size: cover;
      background-position: center center;
      width: auto;
      height: auto;
    }
  }


.logosizefooter{
    margin-top: 4%;
    width: 10vw;
    height: auto;
}

.iconsize{
      padding-right: 5px;
      display: flex;
      flex-direction: row;
      justify-content: center;
}

.d-flex {
    display: flex!important;
    flex-direction: row;
    flex-wrap: nowrap;
    align-content: center;
    justify-content: center;
    align-items: center;
}

.imgcontact img{
    margin-right: 2% !important;
    margin-bottom: 5%;
}





a{
	color:#fff !important;
}

.dropdown-item{
	color:#000 !important;
}

.btn-link {
	color:#fff !important;
}
.itemsNavbar{
	color: #fff;
	margin-right: 2%;
	display: flex;
	flex-direction: row;
	flex-wrap: nowrap;
	align-items: flex-end;
	justify-content: flex-end;
}

.navbar a:hover{
	padding: 5px;
	border-radius: 20px;
	border: 3px solid white;
}


.navbar-nav {
    display: flex !important;
    flex-direction: row !important;
    padding-left: 0 !important;
    margin-bottom: 0 !important;
    list-style: none !important;
    flex-wrap: nowrap !important;
}


.navbar-toggler:focus {	
	box-shadow: none;
}
	

* {
  margin: 0;
  padding: 0;

  font-family: "Poppins", sans-serif;
  font-style: normal;
}

.bnt-secundary {
  background-color: transparent;
  border: 2px solid #fff;
  font-size: 90%;
  color: #fff;
  padding: 1%;

}

.bnt-secundary:hover{
  background-color: transparent;
  border: 2px solid #fe9759;
  color:#fe9759;

}


body {
  overflow-x: hidden;
}

.logosize {
  max-width: 80%;
  margin-top: 0%;
}

@media (max-width: 900px) {
  .logosize {
    width: 50%;
  }
}

@media (max-width: 576px) {
  .logosize {
    width: 30%;
  }
}

.textcolor {
  color: white;
}

.h-title {
  font-size: 200%;
  line-height: 100%;
  font-weight: 700;
}

.h-subtitle {
  font-size: 100%;
}

.p-title {
  font-size: 150%;
}

.bold {
  font-weight: 700;
}

.p-subtitle {
  font-size: 90%;
}


.p-ingredients{
  font-size: 70%;
}
.centralized {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
}

.alignleft {
  text-align: left;
}

.alignright{
  text-align: right;
}

#indicator {
  position: top;
  margin-top: 0px;
  margin-bottom: 10px;
  left: 5%;
  bottom: 0;
  width: 12%;
  height: 1.5%;
  background-color: #fe9759;
  border-radius: 5px;
}

#indicator2 {
  margin: auto;
  width: 10%;
  height: 15%;
  background-color: #fe9759;
  border-radius: 5px;
}




#indicator3 {
  margin: auto;
  width: 6%;
  height: 20%;
  background-color: #fe9759;
  border-radius: 5px;
}

#indicator4 {
  margin: auto;
  width: 8%;
  height: 10%;
  background-color: #fe9759;
  border-radius: 5px;
}

.assignature {
  padding: 0;
  margin: 0;
  margin-bottom: 2%;
  text-align: left;
  font-size: 300%;
  font-weight: 400;
  font-family: "Qwitcher Grypen", cursive;
}

@media (max-width: 900px) {
  .h-title {
    font-size: 5vh;
  }
}

@media (max-width: 900px) {
  .p-title {
    font-size: 3vh;
  }
}

@media (max-width: 900px) {
  .h-subtitle,
  .p-subtitle {
    font-size: 2vh;
  }
}

body {
  background: url(/static/media/BG_top.502feaf9.png);
  background-size: 100%;
  background-repeat: no-repeat;
  background-position: top center;
